import React from "react";
import Layout from "../components/layout";
import SEO from "../components/seo";
import ShowcaseCard from "../components/showcase-card.js";
import CTA from "../components/cta";
import { useStaticQuery, graphql } from "gatsby";

const Showcase = () => {
  const data = useStaticQuery(graphql`
    {
      showcase: file(relativePath: { eq: "showcase/showcase-1.png" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);
  return (
    <Layout>
      <SEO title="Showcase" />
      <div className="bg-accent-pastel-blue">
        <div className="container mx-auto">
          <div className="flex flex-col sm:py-24 py-12">
            <div className="flex flex-col">
              <h1 className="text-center">Made with Landify UI Kit</h1>
              <div className="flex flex-col xl:w-1/2 lg:w-2/3 w-full self-center">
                <p className="text-lg text-center py-4">
                  Check out some of the landing pages created with Landify UI
                  kit by changing typeface, colors, and combining multiple
                  blocks.
                </p>
                <p className="text-lg text-center">
                  <a className="font-medium text-brand-purple-900" href="/">
                    Submit your work
                  </a>{" "}
                  and become a part of showcase.
                </p>
              </div>
            </div>
          </div>
          <div className="grid sm:grid-cols-2 grid-cols-1 lg:col-gap-20 md:col-gap-8 col-gap-4 row-gap-20 pb-20 justify-self-center">
            <ShowcaseCard
              link="https://aravind.design"
              cover={data.showcase.childImageSharp.fluid}
              title="Project management app website"
              designer="Aravind Little Jack"
            />
          </div>
        </div>
      </div>
      <CTA />
    </Layout>
  );
};

export default Showcase;
